import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Container from "@mui/material/Container";
import React, { useState } from "react";
import FormatListBulletedIcon from "@mui/icons-material/FormatListBulleted";
import IconButton from "@mui/material/IconButton";
import makeStyles from "@mui/styles/makeStyles";
import {
  SearchBar,
  BottomNav,
  CustomChip,
  CardKamar,
  TopNav,
  ActionDialog,
} from "../../components";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { connect } from "react-redux";
import { getDataKamar } from "../../redux/actions";
import { FormControl, InputLabel, MenuItem, Select } from "@mui/material";

const useStyles = makeStyles(() => ({
  textContent: {
    padding: "0 8px !important",
    flexGrow: 100,
  },
  filterContent: {
    flexGrow: 100,
  },
  searchBar: {
    display: "flex",
  },
  root: {
    paddingTop: "16px",
  },
  chip: {},
}));

const RoomsPage = (props) => {
  const { actGetDataKamar, stateKamar } = props;
  const classes = useStyles();
  const navigate = useNavigate();

  React.useEffect(() => {
    actGetDataKamar();
  }, []);
  const [arrKamar, setArrKamar] = React.useState([]);
  const [kelasList, setKelasList] = React.useState([]);
  const [kamarList, setKamarList] = React.useState([]);
  const [filterOpen, setFilterOpen] = React.useState(false);
  const [search, setSearch] = React.useState("");

  const [filter, setFilter] = React.useState({
    kelas: "",
    ruang: "",
  });
  const handleSubmitSearch = () => {
    return setFilter({
      kelas: search,
      ruang: search,
    });
  };
  const handleChangeRoom = (val) => {
    return setFilter({
      ...filter,
      ruang: filter.ruang === val ? "" : val,
    });
  };
  const handleChangeKelas = (el) => {
    let val = el.target.value;
    return setFilter({
      ...filter,
      kelas: filter.kelas === val ? "" : val,
    });
  };
  React.useEffect(() => {
    if (stateKamar) {
      let aw = stateKamar.filter((el) => {
        if (filter.kelas && filter.ruang) {
          return (
            el.ruang.toLowerCase() === filter.ruang.toLowerCase() &&
            el.kelas.toLowerCase() === filter.kelas.toLowerCase()
          );
        } else {
          return (
            el.ruang.toLowerCase() === filter.ruang.toLowerCase() ||
            el.kelas.toLowerCase() === filter.kelas.toLowerCase()
          );
        }
      });
      if (!filter.kelas && !filter.ruang) {
        setArrKamar(stateKamar);
      } else {
        setArrKamar(aw);
      }
    }
  }, [filter, stateKamar]);
  React.useEffect(() => {
    if (stateKamar) {
      let kamar = [];
      let kelas = [];
      stateKamar.map((item) => {
        kamar.push(item.ruang);
        kelas.push(item.kelas);
      });
      setKamarList([...new Set(kamar)]);
      setKelasList([...new Set(kelas)]);
    }
    setArrKamar(stateKamar);
  }, [stateKamar]);

  return (
    <>
      <TopNav
        title={"Info Kamar"}
        subtitle="Temukan kamar yang tersedia disini"
        variant="with-background"
      />
      <ActionDialog
        open={filterOpen}
        setOpen={setFilterOpen}
        onSubmit={() => setFilterOpen(false)}
        label={"Kelas"}
        buttonText="Filter"
      >
        <Stack spacing={2}>
          <FormControl fullWidth required>
            <InputLabel id="jenisId">Kelas</InputLabel>
            <Select
              labelId="jenisId"
              id="jenisId"
              name="jenisId"
              value={filter.kelas}
              onChange={handleChangeKelas}
            >
              {kelasList.map((item, index) => {
                return (
                  <MenuItem key={"kelas_" + index} value={item}>
                    {item}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
        </Stack>
      </ActionDialog>
      <Container className={classes.root}>
        <Stack spacing={2}>
          <div className={classes.searchBar}>
            <SearchBar
              label="Cari Kamar"
              onChange={(el) => setSearch(el.target.value)}
              value={search}
              onSubmit={handleSubmitSearch}
            />
            <Paper>
              <IconButton onClick={() => setFilterOpen(!filterOpen)}>
                <FormatListBulletedIcon />
              </IconButton>
            </Paper>
          </div>
          <Swiper
            pagination={true}
            slidesPerView={4}
            spaceBetween={10}
            className="mySwiper"
          >
            {kamarList.map((item, index) => {
              return (
                <SwiperSlide
                  key={"carousel_" + index}
                  onClick={() => handleChangeRoom(item)}
                >
                  <CustomChip
                    variant={filter.ruang === item ? "selected" : "default"}
                    label={item}
                  />
                </SwiperSlide>
              );
            })}
          </Swiper>
          <div>
            <Grid container spacing={2}>
              {arrKamar &&
                arrKamar.map((kamar, index) => {
                  return (
                    <Grid item xs={12} md={4} key={"kamar" + index}>
                      <CardKamar
                        ruang={kamar.ruang}
                        kapasitas={kamar.kapasitas}
                        kelas={kamar.kelas}
                        terisi={kamar.terisi}
                        kosong={kamar.kosong}
                      />
                    </Grid>
                  );
                })}
            </Grid>
          </div>
        </Stack>
        <BottomNav />
      </Container>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateKamarMulti: state.master.kamarMulti,
    stateKamar: state.master.kamar,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actGetDataKamar: (body, action) => dispatch(getDataKamar(body, action)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RoomsPage);
